.link_detalle {
  color: white;

  &:hover {
    color: var(--sec3);
  }

  .row_elementoDes {
    cursor: pointer;

    .img_elemento {
      width: 100%;
    }

    .col_dat-elemento {
      display: flex;
      align-items: center;
      // font-family: "Cabin", sans-serif;

      &.izq {
        justify-content: left;
      }

      &.der {
        justify-content: right;
      }

      .div_dat-elemento {
        &.izq {
          padding-left: 40px;
        }

        &.der {
          padding-right: 40px;
        }
      }

      .h1_dat-elemento {
        margin: 0;
        font-size: 74px;
        // font-family: "Cabin", sans-serif;
        font-weight: bold;
      }

      .h6_dat-elemento {
        margin: 0;
        font-size: 24px;
      }
    }
  }
}
