.datos-sec {
  background-color: var(--pri1);
  color: var(--sec5);
  padding-top: 70px;
  padding-bottom: 70px;

  .col-elemento {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .svg-elementos {
      width: 90px;
    }

    .texto-elementos {
      text-align: center;
      margin-top: 25px;
    }
  }
}

/* =================== MEDIA QUERIES (MEDIUM DEVICES) =================== */
@media screen and (min-width: 1024px) {
  .datos-sec {
    .col-elemento {
      margin-left: 50px;
      margin-right: 50px;
    }
  }
}

/* =================== MEDIA QUERIES (SMALL DEVICES) =================== */
@media screen and (max-width: 600px) {
  .datos-sec {
    .col-elemento {
      margin-bottom: 40px;
      margin-top: 40px;
    }
  }
}
