//------------ Hero -------------
.sec-hero {
  .car-item {
    background-color: black !important;
    max-height: 100vh;
  }

  .car-img-hero {
    opacity: 0.65 !important;
    width: 100%;
  }

  .car-item-caption {
    top: 47%;
  }
}

// CELULARES
@media screen and (max-width: 600px) {
  .sec-hero {
    .car-item {
      max-height: 100vh;
    }

    .car-item-caption {
      top: 43%;
    }

    .carousel-control-next-icon {
      top: 70% !important;
    }
  }
}
