.btn-whatsapp {
  background: var(--sec3);
  color: white;
  border-radius: 10%;

  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;

  bottom: 1.25rem;
  right: 1.3rem;

  height: 3.625rem;
  width: 3.625rem;

  z-index: 50;

  &:hover {
    color: white !important;
  }
}

//------------ WhatsApp Button -------------
@media screen and (min-width: 64em) {
  .btn-whatsapp {
    line-height: 3.125rem;

    right: 1rem;
    bottom: 3.125rem;

    height: 3.125rem;
    width: 3.125rem;
  }
}
