.row_gestion {
  background-color: var(--sec1);
  text-align: center;
  padding: 5px;
  padding-top: 7rem;
  padding-bottom: 7rem;
  font-size: 20px;
}

/* =================== MEDIA QUERIES (SMALL DEVICES) =================== */
@media screen and (max-width: 600px) {
  .row_gestion {
    background-color: var(--sec1);
    text-align: left;
    padding-top: 7rem;
    padding-bottom: 7rem;
    font-size: 15px;
  }
}
