.sec-footer {
  .derechos {
    margin-bottom: 0px !important;
  }

  .col-img-footer {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .ig-icon {
    margin-left: 5px;
  }
}

/* =================== MEDIA QUERIES (SMALL DEVICES) =================== */
@media screen and (max-width: 600px) {
  .sec-footer {
    .col-img-footer {
      margin-bottom: 20px;
    }

    .img-footer {
      width: 90%;
    }
  }
}
